import { Button, Divider, Layout, message, Row, Typography, Space } from 'antd'
import React from 'react'
import { footer, header } from './data'
import './DefaultLayout.sass'
import Home from './Home'
import LeadCollectionForm from './LeadCollectionForm'

const { Header, Content, Footer } = Layout
const { Paragraph } = Typography

class DefaultLayout extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: false
        }
    }

    onCreate = values => {
        // console.log('Received values of form: ', values)
        message.success('Thank you for showing interest. We will reach out to you shorty.')
        this.setState({
            visible: false
        })
    }

    render() {
        const { visible } = this.state
        return (
            <Layout>
                <Header>
                    <span className="logo">{header.logo}</span>
                    <Space size="large">
                        {
                            header.menu.map(item => (
                                <Button
                                    key={`menu-${item}`}
                                    style={{ color: "#fff" }}
                                    type="link"
                                >
                                    {item}
                                </Button>
                            ))
                        }
                        <Button
                            onClick={() => {
                                this.setState({
                                    visible: true
                                })
                            }}
                            shape="round"
                            type="primary"
                        >
                            {header.btn_title.toUpperCase()}
                        </Button>
                    </Space>
                </Header>
                <Content className="site-layout" style={{ marginTop: 64 }}>
                    <Home />
                </Content>
                <Footer>
                    <Row justify="center">
                        <div className="logo">{footer.logo}</div>
                    </Row>
                    <Divider />
                    <Row justify="center">
                        <Paragraph>©2020 {footer.title}</Paragraph>
                    </Row>
                </Footer>
                <LeadCollectionForm
                    visible={visible}
                    onCreate={this.onCreate}
                    onCancel={() => {
                        this.setState({
                            visible: false
                        })
                    }}
                />
            </Layout>
        )
    }
}

export default DefaultLayout