import { Button, Card, Carousel, Col, message, Row, Space, Typography } from 'antd'
import React, { Component } from 'react'
import { howitworks, landing, offerings, testimonials } from './data'
import './Home.sass'
import LeadCollectionForm from './LeadCollectionForm'

const contentStyle = {
	height: '160px',
	color: '#fff',
	lineHeight: '160px',
	textAlign: 'center',
	background: '#364d79'
}

const { Meta } = Card
const { Paragraph, Title } = Typography

class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			visible: false
		}
	}

	onCreate = values => {
		//console.log('Received values of form: ', values)
		message.success('Thank you for showing interest. We will reach out to you shorty.')
		this.setState({
			visible: false
		})
	}

	render() {
		const { visible } = this.state
		return (
			<React.Fragment>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="1"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title>{landing.title.toUpperCase()}</Title>
						<Title level={4}>{landing.sub_title}</Title>
						<Button
							onClick={() => {
								this.setState({
									visible: true
								})
							}}
							shape="round"
							size="large"
							type="primary"
						>
							{landing.btn_title.toUpperCase()}
						</Button>
					</Space>
				</Row>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="2"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title level={2}>{howitworks.title.toUpperCase()}</Title>
						<Row
							gutter={[48, 48]}
							justify="center"
						>
							{
								howitworks.content.map((item, index) => (
									<Col
										key={`howitwork-${index}`}
										lg={8}
										md={12}
										xs={20}
									>
										<Card
											bodyStyle={{ textAlign: "center" }}
											bordered={false}
										>
											<Meta
												title={item.title}
												description={item.sub_title}
											/>
										</Card>
									</Col>
								))
							}
						</Row>
						<Button
							onClick={() => {
								this.setState({
									visible: true
								})
							}}
							shape="round"
							size="large"
							type="primary"
						>
							{landing.btn_title.toUpperCase()}
						</Button>
					</Space>
				</Row>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="3"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title level={2}>{offerings.title.toUpperCase()}</Title>
						<Title level={4}>{offerings.sub_title}</Title>
						{
							offerings.content.map((offering, index) => (
								<Row
									gutter={[48, 48]}
									justify="center"
									key={`offering-${index}`}
								>
									<Col
										md={12}
										push={index % 2 !== 0 ? 12 : 0}
										xs={20}
									>
										<Card
											bodyStyle={{ textAlign: "center" }}
											bordered={false}
										>
											<Meta
												title={offering.title}
												description={offering.sub_title}
											/>
										</Card>
									</Col>
									<Col
										md={12}
										pull={index % 2 !== 0 ? 12 : 0}
										xs={20}
									>
									</Col>
								</Row>

							))
						}
					</Space>
				</Row>
				<Row
					align="middle"
					className="container-row"
					key="4"
					justify="center"
					id="testimonials"
				>
					<Col span="12">
						<Title level={2} style={{ textAlign: "center" }}>{testimonials.title.toUpperCase()}</Title>
						<Carousel autoplay>
							{
								testimonials.content.map((testimonial, index) => (
									<Card>
										<Title level={3}>{testimonial.title}</Title>
										<Paragraph>{testimonial.sub_title}</Paragraph>
									</Card>
								))
							}
						</Carousel>
					</Col>
				</Row>
				<LeadCollectionForm
					visible={visible}
					onCreate={this.onCreate}
					onCancel={() => {
						this.setState({
							visible: false
						})
					}}
				/>
			</React.Fragment>
		)
	}
}
export default Home